@import '../../styles/base/variables';

.header {
    padding: 30px 0;

    &__inner {
        position: relative;
        display: flex;
        align-items: center;
        column-gap: 50px;
        row-gap: 20px;
        z-index: 9999;

        @include breakpoint(xxs) {
            column-gap: 25px;
        }
    }
}