@import '../../styles/base/variables';

.blog-page {
    margin: auto;
    padding: 30px 0 50px;

    & h1 {
        text-align: center;
        font-size: 30px;
        font-weight: 400;
        margin-bottom: 20px;

        @include breakpoint(md) {
            font-size: 24px;
        }
    }

    & h2 {
        font-size: 20px;
        font-weight: 700;
        margin-bottom: 15px;
    }

    & p {
        font-size: 18px;
        margin-bottom: 25px;
    }

    & ul {
        margin-bottom: 25px;
    }

    & ol {
        margin-bottom: 25px;
    }

    & li {
        margin-bottom: 15px;
    }

    & img {
        width: 100%;
        height: 400px;
        margin: auto;
        border-radius: 20px;
        margin-bottom: 40px;
        object-fit: cover;
    }

    & .blog-img-small {
        width: 48%;
        margin: 0px 10px 40px;
        display: inline-block;

        @include breakpoint(lg) {
            width: 100%;
            margin: 0;
            margin-bottom: 30px;
        }
    }
}