.how-work{padding:70px 0 100px;background-position:left 50%;background-repeat:no-repeat;background-size:597px 475px}@media(max-width: 1024px){.how-work{background-size:0;padding:25px 0 100px}}.how-work__title{margin-bottom:60px;font-size:42px;font-weight:700}@media(max-width: 1024px){.how-work__title{font-size:32px;margin-bottom:40px}}.how-work__list{padding-left:500px;counter-reset:section;counter-increment:my-counter}@media(max-width: 1024px){.how-work__list{padding-left:0}}.how-work__item{position:relative;padding-left:90px}.how-work__item:not(:last-child){margin-bottom:25px}.how-work__item::after{content:"Шаг " counter(section);counter-increment:section;display:block;position:absolute;top:0px;left:0px;font-size:13px;border-radius:100px;background:#ffd600;padding:5px 15px}@media(max-width: 576px){.how-work__item::after{padding:5px 10px;font-size:11px}}@media(max-width: 576px){.how-work__item{padding-left:65px}}.how-work__head{display:inline-block;margin-bottom:14px;font-size:18px}.how-work__text{font-size:15px}