.form {

    &__fieldset {
        margin-bottom: 20px;
    }

    &__head {
        font-size: 28px;
        margin-bottom: 20px;
    }

    &__label {
        display: block;
        width: 100%;

        &:not(:last-child) {
            margin-bottom: 10px;
        }
    }

    &__input {
        padding: 10px;
        width: 100%;
        border-radius: 5px;
        border: 1px solid rgb(159, 159, 159);
        font-size: 14px;
    }

    &__error {
        font-size: 14px;
        color: red;
    }

    &__btn {
        width: 100%;
        margin: auto;
        margin-top: 20px;
        padding: 15px 35px;
        border-radius: 20px;
        overflow: hidden;
        border: 1px solid #c4a400;
        background-color: #ffd600;
        transition: 0.3s background-color;

        &:hover {
            background-color: #e7c000;
        }
    }
}
