@import '../../styles/base/variables';

.footer {
    background-color: #fbfbfb;
    padding-bottom: 20px;

    &__inner {
        display: flex;
        justify-content: space-between;
        column-gap: 50px;
        row-gap: 30px;
        padding: 50px 0;
    }

    &__social {}

    &__social-head {
        display: block;
        margin-bottom: 10px;
    }

    &__column {
        &--contacts {
            @include breakpoint(md) {
                display: none;
            }
        }
    }

    &__heading {
        font-size: 18px;
        font-weight: 700;
        margin-bottom: 15px;
    }

    &__list {}

    &__item:not(:last-child) {
        margin-bottom: 10px;
    }

    &__link {
        color: #484848;

        &:hover {
            text-decoration: underline;
        }
    }

    &__bottom {}

    &__copy {}

    & .logo {
        display: block;
        margin-bottom: 30px;
    }

    & .social-networks__link {
        background-color: #f8f8f8;
        border: 1px solid #d4d4d4;
        border-radius: 5px;
        padding: 5px 10px;
    }

    & .social-networks__text {
        display: none;
    }

    & .social-networks__icon--telegram {
        fill: #039be5;
    }

    & .social-networks__icon--whatsapp {
        fill: #4caf50;
    }

    & .menu__list {
        flex-direction: column;
        align-items: flex-start;
    }
}